.heat {
  display: none;
	min-height: 540px;
	padding-bottom: 70px;
  margin-top: 70px;
	background-image: url("../img/back4s.jpg");
	background-repeat: no-repeat;
	background-position: center -300px;
}

.heat__title {
	max-width: 650px;
	margin: 0 auto;
	color: #3a3a3a;
	font-size: 30px;
	font-weight: 700;
	text-align: center;
}

.heat__mark-list {
	margin: 50px auto;
	padding: 0;
	max-width: 243px;
	list-style: none;
	text-align: right;

}

.heat__mark-item {
	margin-bottom: 28px;
	font-size: 18px;
	font-weight: 500;
}

.heat__diff {
	display: flex;
  flex-wrap: wrap;
	justify-content: space-between;
	padding: 10px;
	background-color: #3a3a3a;
}

.heat__diff-title {
	padding: 0 25px;
	color: #ffffff;
	font-size: 20px;
	font-weight: 500;

  &--adv2 {
    padding-right: 25px;
  }
}

.heat__diagram-list {
	@include list;
	flex-basis: 700px;
}

.heat__diagram-item {
	display: flex;
	align-items: center;
  flex-direction: column;
}

.heat__diagram-title {
	flex-basis: 25px;
	margin-right: 20px;
	color: #ffffff;
	font-size: 16px;
	font-weight: 500;
	line-height: 22.01px;
}

.heat__diagram-data {
	color: #ffffff;
	font-size: 16px;
	font-weight: 500;
	line-height: 22.01px;
}

.heat__diagram {
	width: 40%;
	height: 28px;
	background-color: yellow;

	&--2 {
		width: 25%;
	}
}

@media screen and (min-width: 768px) {
  .heat {
    display: block;
  }

  .heat__diff {
    flex-wrap: nowrap;
    padding: 10px 60px;
  }

  .heat__diff-title--adv2 {
    padding-right: 160px;
  }

  .heat__diagram-title {
    flex-basis: 105px;
  }

  .heat__diagram-item {
    flex-direction: row;
  }

  .heat__diagram-data {
    margin-left: auto;
  }

}
